<template>
  <div id="app">
      <div class="loading" v-loading="loadingF"  v-if="loadingF">
        <span>加载中（{{tlen}}/14）</span>
        <div>
          <img src="./img/home1.png" alt="" @load="loadingFC()">
          <img src="./img/home2.png" alt="" @load="loadingFC()">
          <img src="./img/home3.png" alt="" @load="loadingFC()">
          <img src="./img/home4.png" alt="" @load="loadingFC()">
          <img src="./img/Bow.png" alt="" @load="loadingFC()">
          <img src="./img/hometext.png" alt="" @load="loadingFC()">
          <img src="./img/yue.png" alt="" @load="loadingFC()">
          <img src="./img/yue2.png" alt="" @load="loadingFC()">
          <img src="./img/yue3.png" alt="" @load="loadingFC()">
          <img src="./img/yue4.png" alt="" @load="loadingFC()">
          <img src="./img/l1.jpg" alt="" @load="loadingFC()">
          <img src="./img/l2.jpg" alt="" @load="loadingFC()">
          <img src="./img/l3.jpg" alt="" @load="loadingFC()">
          <img src="./img/l4.jpg" alt="" @load="loadingFC()">
          <audio src="./components/music/HS.mp3"></audio>
        </div>
      </div>
    <router-view></router-view>
    <div class="operate" >
      <button class="butz" style="  position: absolute; left:18%;" ref="but1"><el-button type="primary" icon="el-icon-house" circle @click="goHome"></el-button></button>
      <button class="butz" style="  position: absolute; right:50%; transform: translateX(50%);" ref="but2"><el-button type="primary" icon="el-icon-postcard" circle @click="goIntroduce"></el-button></button>
      <button class="butz" style="  position: absolute; right:18%;" ref="but3"><el-button type="primary" icon="el-icon-reading" circle @click="goContent"></el-button></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      loadingF:true,
      tlen:0
    }
  },
  methods:{
    loadingFC(){
      this.tlen=this.tlen+1;
      if(this.tlen==14){
        this.loadingF=false;
        if(this.$route.name==null){
          this.goHome();
        }
      }
    },
    goHome(){
      if(this.$route.name=="introduce"){
        this.$bus.$emit('divFlag2',false);
      }
      if(this.$route.name=="content"){
        this.$bus.$emit('divFlag3',false);
      }
      setTimeout(()=>{
        this.$router.push({ name:'home'});
      },500);
    },
    goContent(){
      if(this.$route.name=="home"){
        this.$bus.$emit('divFlag1',false);
      }
      if(this.$route.name=="introduce"){
        this.$bus.$emit('divFlag2',false);
      }
      setTimeout(()=>{
        this.$router.push({ name:'content'});
      },500);
    },
    goIntroduce(){
      if(this.$route.name=="home"){
        this.$bus.$emit('divFlag1',false);
      }
      if(this.$route.name=="content"){
        this.$bus.$emit('divFlag3',false);
      }
      setTimeout(()=>{
        this.$router.push({ name:'introduce'})
      },500);
    },
  },
  watch:{
    '$route'(to){
      if(to.path=="/home"){
        this.$refs.but1.style.background= '#67C23A';
        this.$refs.but2.style.background= 'rgba(255, 105, 180, 0.671)';
        this.$refs.but3.style.background= 'rgba(255, 105, 180, 0.671)';
      }
      if(to.path=="/content"){
        this.$refs.but1.style.background= 'rgba(255, 105, 180, 0.671)';
        this.$refs.but2.style.background= 'rgba(255, 105, 180, 0.671)';
        this.$refs.but3.style.background= '#67C23A';
      }
      if(to.path=="/introduce"){
        this.$refs.but1.style.background= 'rgba(255, 105, 180, 0.671)';
        this.$refs.but2.style.background= '#67C23A';
        this.$refs.but3.style.background= 'rgba(255, 105, 180, 0.671)';
      }
    }
  },
  mounted(){
    if(this.$route.name=="home"){
      this.$refs.but1.style.background= '#67C23A';
    }
    if(this.$route.name=="introduce"){
      this.$refs.but2.style.background= '#67C23A';
    }
    if(this.$route.name=="content"){
      this.$refs.but3.style.background= '#67C23A';
    }
    this.$store.dispatch('activitiesHistoryChange');
    if(this.$route.name!=null){
      this.loadingF=false;
    }
  }
}
</script>

