        width:100%;
        transform-style: preserve-3d;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 1s;
    }
    .setdz div{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        /* width: 100px; */
        height: 100%;
        /* border: 1px solid red; */

    }
