    }
    .bu2{
        position: absolute;
        z-index: 21;
        right: 10%;
        bottom: 0%;

    } */
    .ctrl_but{
        position: absolute;
        z-index: 21;
        right: 5%;
        top: 5%;
        /* border: 1px solid red; */
        display: flex;
        justify-content: center;
        align-items: center;
        transform: rotate(90deg) translateX(50%) translateY(-280%);
    }
    .ctrl_but button{
        transform: rotate(-90deg);
    }
    .v-enter-active{
        animation: c 0.8s;
    }

    .v-leave-active{
        animation: c 0.8s reverse;
    }

    @keyframes c{
        0%{
             opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }
