<template>
  <div id="home">
    <div class="standingPainting1">
        
      <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInBottomRight"
        leave-active-class="animate__fadeOutBottomRight"
      >
        <img class="imgBOW" src="../img/Bow.png" alt="" key="1" v-if="stFlag">
      </transition>
        
      <transition-group
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInRight"
        leave-active-class="animate__fadeOutRight"
      >
      <img class="imgSP" src="../img/home1.png" alt="" v-show="hFlag[0]" v-if="stFlag" key="S1">
      <img class="imgSP" src="../img/home2.png" alt="" v-show="hFlag[1]" v-if="stFlag" key="S2">
      <img class="imgSP" src="../img/home3.png" alt="" v-show="hFlag[2]" v-if="stFlag" key="S3">
      <img class="imgSP" src="../img/home4.png" alt="" v-show="hFlag[3]" v-if="stFlag" key="S4">
      </transition-group>
  

      <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInBottomLeft"
        leave-active-class="animate__fadeOutBottomLeft"
      >
        <img class="imgTEXT" src="../img/hometext.png" alt="" v-if="stFlag">
      </transition>
      <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInRight"
        leave-active-class="animate__fadeOutRight"
      >
      <el-button type="success" icon="el-icon-refresh" circle key="B2" class="homeBut" v-if="stFlag" @click="refresh"></el-button>
      </transition>
      <el-link href="https://beian.miit.gov.cn/#/Integrated/index" target="_blank" id="ICP">备案号:苏ICP备2022018898号-1</el-link>
    </div>
  </div>
</template>

<script>
import 'animate.css';
export default {
  name: 'vueHome',
  props: {
  },
  data() {
    return {
      stFlag:true,
      hFlag:[true,false,false,false]
    };
  },
  methods: {
    refresh(){
      let zero=this.hFlag[3];
      for (let index = this.hFlag.length-1; index >=0; index--) {
        if(index == 0){
          this.$set(this.hFlag,index,zero);
          // this.hFlag[index]=zero;
        }
        else{
          this.$set(this.hFlag,index,this.hFlag[index-1]);
          // this.hFlag[index]=this.hFlag[index-1];
        }
      }
    }
  },
  mounted(){
    this.$bus.$on('divFlag1',(f)=>{
      this.stFlag=f;
    })

  }
}
</script>

